.i{
    display: flex;
    height: 100vh;
    overflow: hidden;
}

.i-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right{
    flex:1;
    position: relative;
}

.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 30px;
    font-size: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 50px;
    animation:move 10s ease-in-out infinite alternate;
}

@keyframes move {
    25%{
       transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
       transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}

.i-title-item{
    height:50px;
    font-size: 30px;
    font-weight:bold;
    color: #59b256;
    display: flex;
    align-items: center;
}

.i-img{
    width: 115%;
    height: 105%;
    object-fit: contain;
    position: absolute;
    overflow-y: hidden;
}

.i-bg{
    clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #59b256;
    width: 100%;
    height: 100%;
    position:absolute;
    top:0;
    right:0;
}

.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

@media screen and (max-width:480px){
    .i{
        flex-direction: column;
    }

    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        justify-content: center;
        height: 100%;
    }
    .i-desc{
        display: none;
    }
}